h1, h2, h3, h4, h5, h6 {
  font-family: 'Lato', sans-serif;
  font-weight: 100;
  color: #3E6B85; }

h1 {
  font-size: 60px; }

p, ul {
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  font-weight: 100; }

header {
  margin-top: 100px; }

.prefix {
  display: inline-block;
  width: 150px; }

.container {
  margin-left: 60px; }

ul.meta {
  list-style: none;
  padding: 0; }

a {
  color: #0088cc;
  text-decoration: none;

  &:hover {
    text-decoration: underline; } }



